@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap'); */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "Montserrat", sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-day {
  background-color: #daebfd;
}
.App-night {
  background-color: #282c34;
}

.App-night :is(h1, h2, h3, h4, h5, h6) {
  color: #edeef2;
}

.App-night .MuiPaper-root {
  background-color: #f8f3e6;
}

.App-day .MuiPaper-root {
  background-color: white;
}

.dialog {
  padding: 1rem;
}

.intro {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-flow: column;
          flex-flow: column;
}

.intro img {
  display: block;
  max-height: 340px;
  width: auto;
  height: auto;
}

.dialog .plot {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.dialog .details {
  padding: 1rem;
  background-color: #e6c363;
}

.dialog .rating {
  -webkit-align-self: baseline;
          align-self: baseline;
  margin-bottom: 1rem;
  color: #dca22a;
}

.dialog .detail-buttons {
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.dialog-container .dialog-bar {
  background-color: #edb135;
}

img {
  display: block;
  max-height: 200px;
  width: auto;
  height: auto;
  cursor: pointer;
}

.row .movie-title {
  color: #152583;
  cursor: pointer;
}

.row .add {
  color: #3eb52d;
}

.movie_container-desktop {
  margin-right: 2rem;
  padding-top: 1rem;
}

.movie_container {
  width: 100%;
}
.movie_container-title {
  display: -webkit-flex;
  display: flex;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  -webkit-align-items: center;
          align-items: center;
}

.sms-icon {
  margin-right: 0.5rem;
  color: #282c34;
}

.paper .banner-completed {
  color: #3eb52d;
}

.paper .banner {
  color: #282c34;
  margin: 0;
}
.movie_container-title .search-icon {
  margin-right: 0.5rem;
}

.row .nom-year {
  cursor: pointer;
}
.row .nom-title {
  cursor: pointer;
  color: #152583;
}

.row .drag-handle {
  cursor: pointer;
}

.row .remove {
  margin-left: 1rem;
  color: #aa3333;
}

.container-desktop {
  position: -webkit-sticky;
  position: sticky;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  top: 0;
  padding-top: 1rem;
  margin-right: 0;
  min-width: 30vw;
}

.container-title {
  margin-bottom: 0.5rem;
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.container .icon {
  margin-right: 0.5rem;
}

.nom_subtitle {
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

.nom_buttons {
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 0.5rem;
}

.nom_buttons .discard {
  margin-right: 1rem;
}
.paper .empty {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
}

.empty-icon {
  margin-right: 0.5rem;
}

.homepage {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  padding: 1rem;
  margin-bottom: auto;
}

.search {
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  padding: 1rem;
}

.buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-top: 1rem;
}

.buttons button {
  margin-left: 1rem;
}

.list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
}

.title {
  text-align: left;
}

.error {
  color: #e43c3f;
  font-size: 15px;
}

.homepage .title {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 0rem;
  margin-bottom: 0rem;
}

.homepage .title .movie-icon {
  margin-right: 0.5rem;
}

.homepage .app-bar {
  background-color: #303542;
  margin-bottom: 1rem;
}

.homepage .app-bar-sun {
  background-color: white;
  margin-bottom: 1rem;
}

.homepage .moon {
  color: #f2bd2c;
}

.homepage .sun {
  color: #1e8feb;
}
.homepage .night {
  color: #aab6bb;
}

.homepage .toolbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

